import React, { useState , useEffect} from 'react';
import ArticlesList from '../components/Articles_List';
import CommentsList from '../components/CommentsList';
import articles from './article-content'
import Notfound from './NotFoundPage';
import Upvotes from '../components/Upvotes';
import AddComment from '../components/AddComments';

const ArticlePage = ({ match }) => {

    const name = match.params.name;
    const article = articles.find(article => article.name === name);

    const [articleInfo, setarticleInfo] = useState({ upvotes: 0, comments: [] });

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setarticleInfo(body);
        };
        fetchData();
    }, [name])

    const remaining_articles = articles.filter(article => article.name !== name);
    console.log('remaining ' + remaining_articles);
    if (!article) {
        return < Notfound />;
    }
    return (
        <>
            <h1>{article.title}</h1>
            <Upvotes articleName = {name} upvotes = {articleInfo.upvotes} setArticleInfo = {setarticleInfo}/>
            {article.content.map((paragraph, key) => (
                <p>{paragraph}</p>
            )
            )}
            <CommentsList comments = {articleInfo.comments}/>
            <AddComment articleName = {name} setArticleInfo = {setarticleInfo}/>
            <h2>Other Articles</h2>
            <ArticlesList articles={remaining_articles} />
        </>
    );
}

export default ArticlePage